@import url('https://fonts.googleapis.com/css2?family=Zilla+Slab+Highlight&display=swap');




  .slanted-div {
    width: 65vw; /* Half the viewport width */
    height: 100vh; /* 80% of the viewport height */
    background-color: #f5f900; /* Bright yellow for visibility */
    position: absolute;
    top: 0%; /* 12% from the top */
    right: -13.5%; /* 5% from the right */
    clip-path: polygon(15% 0, 80% 0, 80% 100%, 0% 100%); /* Adjusted for a left-side slant */
  }
  
  .background {
    background-color: #2A3139; /* Dark background color */
    width: 100%;
    height: 100vh; /* Full-screen height */
    overflow: hidden; /* Prevent content overflow */
    position: relative; /* Parent positioning for absolute child */
    
  }

  .slanted-div-text{
    position: absolute;
    font-size: 80px;
    left: 20%;
    top : 40%;
    font-family: 'Zilla Slab Highlight', cursive;
  }


  .second-line{
    white-space: nowrap;
  }

  .title-picture{
    position: absolute;
    top : 66%;
    width : 55%;
    left : 16%;
  }

   .title-text{
    position: absolute;
    font-family: 'Roboto', sans-serif;
    color: #f5f900;
    font-size: 70px;
    top: 28%;
    left : 10%;
  } 

  .title-sub-text{
    position: absolute;
    font-family: 'Roboto', sans-serif;
    color: #f5f900;
    font-size: 40px;
    top: 40%;
    left : 13%;
  }

  .title-sub-text-2{
    position: absolute;
    font-family: 'Roboto', sans-serif;
    color: #f5f900;
    font-size: 20px;
    top: 55%;
    left : 7%;
  }

  .explore-button{
    position: absolute;
    font-family: 'Roboto', sans-serif;
    background-color: #f5f900;
    border : none;
    top: 70%;
    left : 20%;
    width: 170px;
    height : 40px;
    border-radius : 12px;
    font-weight: bold;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }

  .explore-button:hover{
    background-color: #2A3139;
    color : #f5f900;
    border : 1px solid #f5f900;
    cursor : pointer;
  }

  .skills-button{
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
    position: absolute;
    top : 0.5%;
    left : 42.5%;
    width: 50px;
    height: 30px;
    color: #f5f900;
    font-weight: bold;
  }

  .skills-button:hover  {
    width: 50px;
    height: 30px;
    background-color: #f5f900;
    outline: none;
    color: black;
    font-weight: bold;
  }

  .projects-button{
    position: absolute;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
    left : 46.5%;
    top : 0.5%;
    width: 65px;
    height: 30px;
    color: #f5f900;
    font-weight: bold;
  }

  .projects-button:hover{
    width: 65px;
    height: 30px;
    background-color: #f5f900;
    color: black;
    font-weight: bold;
  }

  .aboutme-button{
    position : absolute;
    color : yellow;
    background-color: transparent;
    border: none;
    left : 51.5%;
    font-size: 13px;
    height : 4.5%;
    font-size: 15px;
    transition: background-color 0.3s ease, transform 0.3s ease;
    width : 2.5%;
    top:0.5%;
    white-space: nowrap;
    height: 30px;
    width: 75px;
    font-weight: bold;
    cursor: pointer;
  }

  .aboutme-button:hover{
  height: 30px;
  width: 75px;
  background-color: #f5f900;
  color: black;
  font-weight: bold;
  }

.title-img{
  position: absolute;
  width : 73vw;
  height : 100vh;
  left : 50px;
}



